*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
.app{
  width:100%;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  overflow-x: hidden;
}

/*HEADER*/
.header{
  color: white;
  width:100%;
  position: fixed;
  z-index: 3;
  font-family:Georgia, 'Times New Roman', Times, serif;
}

.header_main{
  justify-content: space-between;
  padding:10px 20px;
  background-color: black;
  height:10vh;
  width:inherit;
  align-items: center;
  display: none;
  position:fixed;
  position: relative;
}
.header_icon{
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-right:15px;
}
.header_icon ul{
  list-style: none;
  display: flex;
}
.header_icon ul li{
  padding:5px;
  border-right:.5px white solid;
  display: flex;
  align-items: center;
}
.header_icon ul li:hover{
  color:gold;
}
.header_icon h2{
  margin-left: 12px;
}
.header_cart{
  border:2px white solid;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right:15px;
  color:white;
  border-radius: 10%;
  margin-left: 10px;
}
.header_cart:hover{
  border:2px gold solid;
  color:gold;
}
.header_cart .header_cart_sum{
  margin-left: 8px;
  font-weight: bold;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.header_side{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:10px 16px;
  width:100%;
  background-color: black;
  height:60px;
  position:relative;
}
.header_side h1{
  transform: translateX(25px);
}
/*CONTENT*/
.content{
  margin-bottom:25px;
}
/*BANNER*/
.banner{
  width:100%;
  position: relative;
  color:white;
  height:90vh;
  background: url("\space.jpg") no-repeat; 
  background-size: cover;
  grid-gap:5px;
  padding:0 20px;
  font-size: 24px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.banner h1{
  text-align: center;
  padding-top:100px;
}
.banner h2{
  text-align: center;
}
.banner img{
  width:100%;
  height:100%;
  min-height: 300px;
  min-width: 400px;
  margin: auto;
}
.banner content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top:15%;
  padding:0 40px;
}
.banner content button{
  margin-top: 30px;
  font-size: 16px;
  background-color: transparent;
  border:white solid;
  color:white;
  border-radius:10px;
  padding:5px;
}
.banner content button:hover{
  border:gold solid;
  color:gold;
}
.banner p{
  display: flex;
  color:white;
  align-items: center;
  text-align: center;
}
/*SECTION*/
.section{
  width:70%;
  margin: auto;
}
.subsection{
  width:100%;
  height:50vh;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content:flex-end;
  align-items: center;
  font-size: 30px;
  margin-bottom: 2em;
  text-align: center;
  position: relative;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.subsection .imgWrap{
  width:100%;
  height:100%;
}
.subsection .imgWrap img{
  width:100%;
  height:100%;
  filter: grayscale(30%);
}

.subsection .subsection_item{
  position: absolute;
  top:0%;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height:70%;
  margin:auto;
}
.subsection .subsection_item h4{
  padding-top: 1em;
  color:white;
}
.subsection .subsection_item button{
  width: 40%;
  margin: 200px auto;
  font-size: 20px;
  background-color: transparent;
  border-radius: 10px;
  font-weight: bolder;
  color:white;
  border:white solid 2px;
}
.subsection .subsection_item button:hover{
  color:black;
  border:black solid 2px;
  background-color: white;
}
/*BLOG*/
.blog{
  width:90%;
  margin: auto;
}
.blog_section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin:50px;
  padding-bottom: 20px;
  border-bottom: black solid;
}
.blog_section h1{
  margin: 20px;
}
.blog_section .blog_content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.blog_section .blog_content img{
  margin-bottom: 30px;
}
/*ABOUT*/
.about_container{
  height:70vh;
}
.about{
  width:90%;
  height: 40vh;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about img{
  max-width: 200px;
  max-height: auto;
}
/*CONTACT*/
.contact_container{
  height:70vh;
}
.contact{
  width:80%;
  margin:auto;
}

/*PRODUCT*/
.product_title{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:5px;
  width:100%;
  margin: 50px 0;
}
.product_section{
  width:100%;
  margin: auto;
}
.product_card{
  display: flex;
  width:80%;
  margin: auto;
  height:100%;
  margin-bottom: 20px;
  border-radius: 5%;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.product_card .imgWrap{
  width:100%;
  height:100%;
  overflow: hidden;
  border-radius: 5%;
}

.product_card .imgWrap img{
  width:100%;
  max-height:200px;
  border-radius: 5% 5% 0 0;
}
.product_card:hover .imgWrap img{
  transform: scale(1.2);
  opacity: .7;
}
.product_card .product_content{
  width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
  justify-self:center;
  align-items: center;
  text-align: center;
}
.product_card .product_content h2{
  font-size:18px;
  opacity: .5;
}
.product_card .product_content h1{
  font-size: 2em; 
}
.product_card .product_content p{
  margin-bottom: 20px;
}
.price{
  display: flex;
  justify-content:space-between;
  align-items: center;
  width:100%;
  padding:0 20px;
}
/*STORE*/ 
.store{
  margin-bottom: 3em;
}
.store_title{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:5px;
  width:100%;
  margin-bottom: 2em;
}
.title_line{
  width:35%;
  height:5px;
  background-color: black;
  border-radius: 20%;
}

.store_card .imgWrap{
  width:100%;
  height:100%;
  overflow: hidden;
  border-radius: 5% 5% 0 0;
}

.store_card .imgWrap img{
  width:100%;
  max-height:300px;
  height: 100%;
  border-radius: 5% 5% 0 0;
}
.store_card:hover .imgWrap img{
  transform: scale(1.2);
  opacity: .7;
}
.store_card .store_content{
  width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
  justify-self:center;
  align-items: center;
  padding: 10px;
}
.store_card .store_content h2{
  font-size:18px;
  opacity: .5;
}
.store_card .store_content h1{
  font-size: 20px;
}

.store_section{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin:auto;
  width:90%;
  margin:auto;
  grid-gap:10px;
}
.store_card{
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 5%;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  
}
.product_btn{
  color:white;
}
/*CART*/
.cart_menu{
  color:white;
  width:50%;
  margin: auto;
  position: absolute;
  top:100%;
  left:100%;
  font-family:'Times New Roman', Times, serif;
  font-size: 20px;
  background-color:rgb(39, 38, 38);
}
.nav_menu{
  color:white;
  width:50%;
  margin: auto;
  position: absolute;
  top:100%;
  right:101%;
  height: 100vh;
  transition: all 1s ease;
  background-color:rgb(39, 38, 38);
  font-family:'Times New Roman', Times, serif;
  font-size: 20px;
}
.nav_menu h1{
  display: flex;
  justify-content: center;
  background-color:rgb(65, 64, 64);
  font-size: 30px;
  transform: translateX(0px);
}
.nav_menu ul{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  list-style: none;
  padding-top: 20px;
  font-weight: bolder;
}
.nav_menu ul li{
  padding:20px 0;
}
.nav_menu ul li:hover{
  color:gold;
}
.cart_section{
  width:100%;
  height: 100vh;
  transition: all 1s ease;
  background-color:rgb(39, 38, 38);
  z-index: 3;
}
.cart_section h1{
  background-color: rgb(65, 64, 64);
  text-align: center;
  transform: translateX(0);
}
.cart_section h3{
  padding:10px;
}
.cart_card{
  display: grid;
  grid-template-columns: 6fr 1fr;
  margin:7px 0;
  padding-bottom: 3px;
  border-bottom: 2px solid rgb(65, 64, 64);
  align-items: center;
  padding:0 20px;
}
.cart_card .delete_btn{
  background-color: rgb(226, 166, 166);
  border:none;
  height:20px;
  width:20px;
  color:white;
}
.cart_card .delete_btn:hover{
  background-color: red;
}
.cart_Qty{
  display: flex;
  height:40px;
  align-items: center;
  color:white;
}
.cart_Qty_btn{
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}
.Qty_btn{
  background-color: transparent;
  border:none;
  color:rgb(65, 64, 64);
}
.Qty_btn:hover{
  color:gold;
}
/*FOOTER*/
.footer{
  height:20vh;
  width:100%;
  background-color: black;
  color:white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.footer .subfoot{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (min-width: 768px){
  .blank{
    height:10vh;
  }
  .header_main{
    display: flex;
  }
  .header_side{
    display: none;
  }
  .banner{
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
  .banner content{
    position: relative;
    top:0%;
  }
  .banner h1{
    padding-top: 0px;
  }
  .banner img{
    min-width:500px;
    min-height: auto;
    transform: translateX(-40px);
  }
  .section{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:10px;
    width:80%;
  }
  .subsection .subsection_item{
    position: absolute;
    top:40%;
    transform: translateY(-50%);
    font-weight: bold;
  }
  .store_section{
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .store_card .imgWrap img{
    max-height:150px;
  }
  .about{
    flex-direction: row;
  }
}